.movieDetail {
    display: flex;
    flex-direction: row;
}

.background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    filter: brightness(50%);
}

.background img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.content {
    margin: auto;
    transform: translate(0, 80%);
    width: 1200px;
    position: relative;
    display: flex;
    flex-direction: row;
    justify-items: center;
    z-index: 1;
}

.title {
    font-size: 30px;
    font-weight: 600;
    color: white;
    text-decoration: none;
    text-shadow: 0px 0px 2px black;
}
.title:hover {
    color: whitesmoke;
    text-shadow: 0px 0px 2px white;
}
.describe {
    margin-left: 20px;
}
.genres {
    margin: 5px;
}