* {
    /* background-color: black; */
    color: white;
}



.movie {
    z-index: 0;
    position: relative;
    margin: 10px;
}

.describe {
    font-size: 12px;
    position: absolute;
    top: 50%;
    left: 3%;
}

.image {
    z-index: 0;
    filter: brightness(50%);
}

.movie:hover {
    filter: brightness(130%);
    text-shadow: 0 0 2px white;
}

.title {
    text-shadow: 4px 2px 10px black;
    font-size: 15px;
    font-weight: 600;
    text-decoration: none;
    color: white;
}

.title:hover {
    color: white;
}

.modal {
    z-index: 3;
    background-color: white;
    color: white;
}